
* {
    margin: 0;
    padding: 0;
}
a {
    text-decoration: none;
}
html, body, #app {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
}

/*#app {
    width: 100%;
    overflow-x: hidden;
    min-height: 100%;
    -webkit-overflow-scrolling: touch;
}*/

/* Make clicks pass-through */
#nprogress {
    pointer-events: none;
}
#nprogress .bar {
    background: #ffc001;
    position: fixed;
    z-index: 1031;
    top: 0;
    left: 0;
    width: 100%;
    height: 0.06rem;
}

/* Fancy blur effect */
#nprogress .peg {
    display: block;
    position: absolute;
    right: 0px;
    width: 2rem;
    height: 100%;
    box-shadow: 0 0 0.2rem #29d, 0 0 0.1rem #29d;
    opacity: 1.0;
    -webkit-transform: rotate(3deg) translate(0.08rem, -4px);
            transform: rotate(3deg) translate(0.08rem, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
    display: block;
    position: fixed;
    z-index: 1031;
    top: 0.3rem;
    right: 0.3rem;
}
#nprogress .spinner-icon {
    width: 0.36rem;
    height: 0.36rem;
    box-sizing: border-box;
    border: solid 0.04rem transparent;
    border-top-color: #ffc001;
    border-left-color: #ffc001;
    border-radius: 50%;
    -webkit-animation: nprogress-spinner 400ms linear infinite;
            animation: nprogress-spinner 400ms linear infinite;
}
.nprogress-custom-parent {
    overflow: hidden;
    position: relative;
}
.nprogress-custom-parent #nprogress .spinner, .nprogress-custom-parent #nprogress .bar {
    position: absolute;
}
@-webkit-keyframes nprogress-spinner {
0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
}
100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
}
}
@keyframes nprogress-spinner {
0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
}
100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
}
}

/* Fancy blur effect end */
#app {
    width: 100%;
    max-width: 15rem;
    margin: 0 auto;
}
@-webkit-keyframes slideInRight {
from {
        -webkit-transform: translate3d(100%, 0, 0);
                transform: translate3d(100%, 0, 0);
        visibility: visible;
}
to {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
}
}
@keyframes slideInRight {
from {
        -webkit-transform: translate3d(100%, 0, 0);
                transform: translate3d(100%, 0, 0);
        visibility: visible;
}
to {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
}
}
@-webkit-keyframes slideInLeft {
from {
        -webkit-transform: translate3d(-100%, 0, 0);
                transform: translate3d(-100%, 0, 0);
        visibility: visible;
}
to {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
}
}
@keyframes slideInLeft {
from {
        -webkit-transform: translate3d(-100%, 0, 0);
                transform: translate3d(-100%, 0, 0);
        visibility: visible;
}
to {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
}
}
@-webkit-keyframes slideInUp {
from {
        -webkit-transform: translate3d(0, 100%, 0);
                transform: translate3d(0, 100%, 0);
        visibility: visible;
}
to {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
}
}
@keyframes slideInUp {
from {
        -webkit-transform: translate3d(0, 100%, 0);
                transform: translate3d(0, 100%, 0);
        visibility: visible;
}
to {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
}
}
@-webkit-keyframes slideOutDown {
from {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
}
to {
        visibility: hidden;
        -webkit-transform: translate3d(0, 100%, 0);
                transform: translate3d(0, 100%, 0);
}
}
@keyframes slideOutDown {
from {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
}
to {
        visibility: hidden;
        -webkit-transform: translate3d(0, 100%, 0);
                transform: translate3d(0, 100%, 0);
}
}
@-webkit-keyframes fadeIn {
from {
        opacity: 0;
}
to {
        opacity: 1;
}
}
@keyframes fadeIn {
from {
        opacity: 0;
}
to {
        opacity: 1;
}
}
@-webkit-keyframes fadeOut {
from {
        opacity: 1;
}
to {
        opacity: 0;
}
}
@keyframes fadeOut {
from {
        opacity: 1;
}
to {
        opacity: 0;
}
}
.animated {
    -webkit-animation-duration: .35s;
            animation-duration: .35s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}
.fadeIn {
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
}
.fadeOut {
    -webkit-animation-name: fadeOut;
            animation-name: fadeOut;
}
.slideInUp {
    -webkit-animation-name: slideInUp;
            animation-name: slideInUp;
}
.slideOutDown {
    -webkit-animation-name: slideOutDown;
            animation-name: slideOutDown;
}
.slide-enter {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
.slide-leave-to {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
.fade-enter {
    opacity: 0;
}
.fade-leave-to {
    opacity: 1;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
.slide-enter-active, .slide-leave-active, .fade-enter-active, .fade-leave-active {
    position: absolute;
    width: 100%;
}
.fade-enter-active {
    /*transition: opacity 5s;*/
}
.fade-leave-active {
    /*transition: transform 8s;*/
}
.slide-enter-active, .slide-leave-active {
    /*transition: transform 35s cubic-bezier(.55, 0, .1, 1);*/
}
.transition {
    -webkit-transition: all .5s cubic-bezier(.55, 0, .1, 1);
    transition: all .5s cubic-bezier(.55, 0, .1, 1);
}



